<template>
  <div class="container-fluid k p-0">
    <span id="explore">Explore</span>
    <button
      id="map"
      class="btn btn-dark locationButton d-block position-fixed"
      @click="getLocation()"
    ></button>
    <div id="maps" class="maps"></div>
  </div>
</template>

<script>
import {} from "vue";
import L from "leaflet";
import circle from "@turf/circle";

export default {
  name: "App",
  components: {},
  setup() {
    function getLocation() {
      // e.preventDefault();
      setTimeout(() => {
        if (!navigator.geolocation) {
          alert("Browser doesn't support geolocation");
        } else {
          navigator.geolocation.getCurrentPosition(success, error);
        }
      }, 500);
    }

    // Get current position successfully
    function success(position) {
      var map,
        marker,
        latitude = position.coords.latitude,
        longitude = position.coords.longitude,
        loadingTimeout;

      loadingTimeout = setTimeout(loading, 600);

      var loading = function () {};
      fetch(
        "https://nominatim.openstreetmap.org/reverse?format=json&lat=" +
          latitude +
          "&lon=" +
          longitude
      )
        .then(function (data) {
          if (loadingTimeout) {
            clearTimeout(loadingTimeout);
            loadingTimeout = null;
            return data.json();
          }
        })
        .then((response) => {
          console.log(response);
          marker
            .bindPopup("<p class='location'>" + response.display_name + "</p>")
            .openPopup();
        })
        .catch((err) => console.log(err));

      // Instance map using leaflet
      map = L.map("maps").setView([latitude, longitude],11);

      // Tile layer using key api at cloudmade.com
      L.tileLayer(
        "https://{s}.basemaps.cartocdn.com/dark_all/{z}/{x}/{y}{r}.png",
        {
          attribution:
            '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors &copy; <a href="https://carto.com/attributions">CARTO</a>',
          subdomains: "abcd",
          maxZoom: 20,
        }
      ).addTo(map);

      // Marker using leaflet
      marker = L.marker([latitude, longitude]).addTo(map);


      var center = [longitude, latitude];
      var radius = 5;
      var options = {
        steps: 10,
        units: "kilometers",
        properties: { foo: "bar" },
      };

      var circle1 = circle(center, radius, options);
      new L.GeoJSON(circle1, { color: "red" }).addTo(map);

     
      // Popup in leaflet
    }

    // Get current position fail
    function error() {
      alert(
        "Get current position fail. Please access codepen to get geolocation."
      );
    }
    return {
      getLocation,
    };
  },
};
</script>

